.successful-operation .successful-center {
  width: 853px;
  height: 459px;
  background: #ffffff;
  box-shadow: 0px 10px 24px 0px #f4f4f4;
  border: 1px solid rgba(0, 0, 0, 0.06);
  padding: 50px 100px 99px;
}
.successful-operation .successful-center img {
  display: block;
  margin: 0 auto;
  margin-bottom: 14px;
}
.successful-operation .successful-center .successful-text {
  text-align: center;
  font-size: 14px;
  margin-bottom: 28px;
}
.successful-operation .successful-center .successful-content {
  width: 652px;
  height: 138px;
  background: #f9f9f9;
  padding: 20px 24px 0 24px;
}
.successful-operation .successful-center .successful-content .successful-content-text {
  margin-bottom: 18px;
  color: #666666;
  clear: both;
}
.successful-operation .successful-center .successful-content .successful-content-text .successful-content-right {
  float: right;
}
.successful-operation .successful-center .successful-btn {
  width: 128px;
  height: 35px;
  margin: 24px auto;
  display: block;
}
@media only screen and (max-width: 768px) {
  .successful-operation .successful-center {
    width: 100%;
    padding: 50px 10px;
  }
  .successful-operation .successful-center .successful-content {
    width: 100%;
    height: 170px;
    padding: 24px 10px;
  }
  .successful-operation .successful-center .successful-btn {
    margin: 24px auto;
  }
}
